/****************

就手收到订单 - 动画

****************/
//动画时间
.animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}
//重复
.animated.infinite {
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
}
//动画时间 2s
.animated.hinge {
    -webkit-animation-duration: 2s;
    animation-duration: 2s;
}

//就手收到订单动画时间
.get-order-animated-set {
    -webkit-animation-duration: 5s;
    animation-duration: 5s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

//就手收到订单动画过程
@keyframes get-order-animated-process {
    0% {
        opacity: 0;
    }
    20% {
        opacity: 1;
    }
    80% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
//定义动画名
.get-order-animated {
    -webkit-animation-name: get-order-animated-process;
    animation-name: get-order-animated-process;
}


/****************

加入购物车 商品图 - 动画

****************/

//加入购物车动画时间
.TurnAround-set {
    -webkit-animation-duration: 0.8s;
    animation-duration: 0.8s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

@keyframes TurnAround {
    0% {
        bottom: 60%;
        left: 45%;
        transform: rotate(0deg);
    }
    20% {
        bottom: 48%;
        left: 39.4%;
        transform: rotate(720deg);
    }
    40% {
        bottom: 36%;
        left: 33.8%;
        transform: rotate(1440deg);
    }
    60% {
        bottom: 24%;
        left: 28.2%;
        transform: rotate(2160deg);
    }
    80% {
        bottom: 12%;
        left: 22.6%;
        transform: rotate(2880deg);
    }
    100% {
        bottom: 0%;
        left: 17%;
        transform: rotate(3600deg);
    }
}

.TurnAround-animated {
    -webkit-animation-name: TurnAround;
    animation-name: TurnAround;
}



/*页面加载中 遮罩层*/
.mask {
    position: absolute;
    top: 0px;
    left: 0px;
    background-color: #eee;
    z-index: 1002;
    opacity:1;
    width: 100vw;
    height: 100vh;
    height: 100%;
}

/*页面加载中 遮罩层动画*/


/*Typing Loader*/
.typing_loader{
    width: 12px;
    height: 12px;
    border-radius: 50%;
    animation: typing .8s linear infinite alternate;
    margin: 0px auto; /* Not necessary- its only for layouting*/
    position: relative;
    left: -24px;
    top: 49%;
}

@keyframes typing{
    0%{
        background-color: rgba(255,0,0, 1);
        box-shadow: 24px 0px 0px 0px rgba(255,0,0,0.2),
        48px 0px 0px 0px rgba(255,0,0,0.2);
    }
    25%{
        background-color: rgba(255,0,0, 0.4);
        box-shadow: 24px 0px 0px 0px rgba(255,0,0,2),
        48px 0px 0px 0px rgba(255,0,0,0.2);
    }
    75%{ background-color: rgba(255,0,0, 0.4);
        box-shadow: 24px 0px 0px 0px rgba(255,0,0,0.2),
        48px 0px 0px 0px rgba(255,0,0,1);
    }
}